import * as React from 'react'
import { Alert, Button, Layout, Toast } from '../auto-ui'

const Demo: React.FC<any> = () => {
  const onClickA = () => {
    Alert({
      title: '',
      desc: '',
      btns: [
        {
          name: '',
          type: 'primary',
          hollow: true,
          onClick: () => {
            Toast('')
          },
        },
      ],
    })
  }

  const onClickB = () => {
    Alert({
      title: '',
      desc: '',
      btns: [{ name: '', type: 'default' }, { name: '', type: 'danger' }],
    })
  }

  const onClickC = () => {
    Alert({
      title: '',
      desc: '',
      btns: [
        { name: '', type: 'default', mini: true },
        { name: '', type: 'primary', mini: true },
      ],
    })
  }

  return (
    <Layout>
      <Layout.Body>
        <Button onClick={onClickA}></Button>
        <br />
        <Button onClick={onClickB}></Button>
        <br />
        <Button onClick={onClickC}></Button>
      </Layout.Body>
    </Layout>
  )
}

export default Demo
